/* src/App.css */
.App {
  width: 100%;
  max-width: 480px;
  margin: 0 auto;
  font-family: 'GmarketSansMedium', sans-serif;
  background: #f8f8f8;
  color: #333;
  overflow-x: hidden;
  min-height: 100vh;
}

section {
  padding: 20px;
  min-height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: transparent;
}
