.container {
  width: 100%;
  padding: 4rem 2rem;
  background-color: #f8f8f8;
}

.content {
  max-width: 1200px;
  margin: 0 auto;
}

.title {
  text-align: center;
  font-size: 1.5rem;
  font-family: 'GmarketSansMedium', sans-serif;
  margin-bottom: 3rem;
  color: #333;
}

.infoMessage {
  text-align: center;
  font-size: 0.8rem;
  line-height: 1.8;
  margin-bottom: 3rem;
  color: #333;
}

.infoText {
  text-align: center;
  font-size: 1rem;
  line-height: 1.8;
  margin-bottom: 3rem;
  color: #333;
}
.infoText:last-child {
  margin-bottom: 0;
}
.info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  text-align: center;
}

.infoItem {
  padding: 2rem;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.infoItem h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #333;
}

.infoItem p {
  font-size: 1.1rem;
  color: #666;
  margin: 0.5rem 0;
}
